<template>
  <v-container fluid fill-height style="padding:0;" id="designPattern" grid-list-md>
    <Blog :items="items" :time="time" :title="title">
      <div class="blogcontent">
        <v-layout row wrap>
          <v-flex sm12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="constructor">构造函数模式</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <p class="subtitle-1"><span class="font-weight-bold">说明</span>：创建新对象，并且为新对象添加属性和方法</p>
                    <p class="body-2">以下是构造方式的三种应用方法</p>
                    <ul>
                        <li>
                            <p>创建对象</p>
                            <pre class="code">{{pres[0]}}</pre>
                        </li>
                        <li>
                            <p>构造器</p>
                            <pre class="code">{{pres[1]}}</pre>
                        </li>
                        <li>
                            <p>利用原型的构造器</p>
                            <pre class="code">{{pres[2]}}</pre>
                        </li>
                    </ul>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>

          <v-flex sm12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="factory">工厂模式</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <p class="subtitle-1"><span class="font-weight-bold">说明</span>：根据传入的类型创建对象</p>
                    
                    <pre class="code">{{pres[3]}}</pre>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>

          <v-flex sm12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="module">模块模式</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <p class="subtitle-1"><span class="font-weight-bold">说明</span>：提供私有和公共的属性和方法</p>
                    
                    <pre class="code">{{pres[4]}}</pre>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>

          <v-flex sm12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="single">单例模式</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <p class="subtitle-1"><span class="font-weight-bold">说明</span>：只会创建一个实例</p>
                    
                    <pre class="code">{{pres[5]}}</pre>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>

          <v-flex sm12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="observer">观察者模式</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <p class="subtitle-1"><span class="font-weight-bold">说明</span>：当一个对象状态发生变化，其他关联的对象能够同步刷新</p>
                    
                    <pre class="code">{{pres[6]}}</pre>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>

          <v-flex sm12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="subpub">发布/订阅模式式</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <p class="subtitle-1"><span class="font-weight-bold">说明</span>：订阅者能够通过函数注册和接收发布者的消息</p>
                    
                    <pre class="code">{{pres[7]}}</pre>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
        </v-layout>
      </div>
    </Blog>
  </v-container>
</template>

<script>
import Blog from "../Blog.vue";
export default {
  components: { Blog },
  data() {
    return {
      pres: 
[
    `
    const obj = {};
    const obj = Object.create(Object.prototype);
    const obj = new Object(); 
    `,
    `
    function Obj(a, b) {
        this.a = a;
        this.b = b;
        this.fn = function() {
            return this.a + "|" + this.b;
        }
    }

    const o = new Obj('aa', 'bb');
    o.fn();
    `,
    `
    function Obj(a, b) {
        this.a = a;
        this.b = b;
        Obj.prototype.fn = function() {
            return this.a + "|" + this.b;
        }
    }

    const o = new Obj('aa', 'bb');
    console.log(o.fn());
    `,
    `
    function Dog(option) {
        this.size = option.size;
    }
    function Cat(option) {
        this.size = option.size;
    }
    function Animal() {}
    Animal.createAnimal = function(option) {
        let animal;
        switch (option.type) {
            case 'dog':
                animal = new Dog(option);
                break;
            case 'cat':
                animal = new Cat(option);
                break;
            default:
                break;
        }
        return animal;
    }

    const myAnimal = Animal.createAnimal({
        type: 'cat',
        size: 'small'
    })

    console.log(myAnimal)
    `,
    `
    const myModule = (function() {
        let aa; // private
        let bb;

        return {
            setAB(a, b) { // public
                aa = a;
                bb = b;
            },
            getAB() {
                return aa + "|" + bb
            }
        }
    })()

    myModule.setAB('a', 'b');
    myModule.getAB();
    `,
    `
    const singlePattern = (function(){
        let instance;
        function createInstance() {
            let myName;
        
            return {
                setName: function(name) {
                    myName = name;
                },
                getName: function() {
                    return myName;
                }
            }
        }
        function getInstance() {
        
            if(!instance){
                instance = createInstance();
            }
            
            return instance;
        }
        
        return {
            getInstance: getInstance
        } 
    })();

    const one = singlePattern.getInstance();
    const two = singlePattern.getInstance();
    console.log(one === two) // true
    one.setName('yejiawei');
    console.log(two.getName()); // "yejiawei"
    `,
    `
    <button id="addNewObserver">Add New Observer checkbox</button>
    <input id="mainCheckbox" type="checkbox"/>
    <div id="observersContainer"></div>

    const controlCheckbox = document.getElementById("mainCheckbox");
    const addBtn = document.getElementById("addNewObserver");
    const container = document.getElementById("observersContainer");

    // 要素一：观察者队列，提供增删改查队列的方法
    const subject = (function() {
        let instance;
        function createInstance() {
            const observerList = [];

            return {
                addObserver: function( observer ){
                    observerList.push( observer );
                },
                notify: function( context ){
                    observerList.forEach(observer => {
                        observer.update(context);
                    })
                }
            }
        }

        function getInstance() {
            if(!instance) {
                instance = createInstance();
            }

            return instance;
        }

        return {
            getInstance: getInstance
        }
    })();
    // 要素二：构建subject
    controlCheckbox.constructor.prototype = Object.assign(controlCheckbox.constructor.prototype, subject.getInstance())
    controlCheckbox.onclick = function () {
        controlCheckbox.notify(controlCheckbox.checked);
    };
    // 要素三：observer的扩展接口，用来处理更新功能
    function Observer(check) {
        check.update = function (value) {
            this.checked = value;
        };
    }
    // 要素四：构建observer
    addBtn.onclick = function () {
        const check = document.createElement("input");
        check.type = "checkbox";
        Observer(check);

        controlCheckbox.addObserver(check);
        container.appendChild(check);
    };
    `,
    `
    const pubAndsub = (function (){
        let instance;
        function createInstance() {
            const topics = {};
            let subUid = -1;
            return {
                publish: function(topic, args) {
                    if ( !topics[topic] ) {
                        return false;
                    }

                    const subscribers = topics[topic];
                    let len = subscribers ? subscribers.length : 0;
                    while (len--) {
                        subscribers[len].func( topic, args );
                    }
                },
                subscribe: function(topic, func) {
                    if (!topics[topic]) {
                        topics[topic] = [];
                    }
                    const id = ( ++subUid ).toString()
                    topics[topic].push({
                        id,
                        func: func
                    });
                    return id;
                },
                unsubscribe: function(id) {
                    for ( let t in topics ) {
                        if ( topics[t] ) {
                            for ( let i = 0, j = topics[t].length; i < j; i++ ) {
                                if ( topics[t][i].id === id ) {
                                    topics[t].splice( i, 1 );
                                    return true;
                                }
                            }
                        }
                    }
                    return false;
                }
            }
        }
        function getInstance() {
            if(!instance) {
                instance = createInstance();
            }

            return instance;
        }

        return {
            getInstance: getInstance
        }
    })();

    const ps = pubAndsub.getInstance();
    const psid = ps.subscribe("test", function(topics, data) {
        console.log( "Logging: " + topics + ": " + data );
    })

    ps.publish( "test", "hello world!" );
    ps.unsubscribe( psid );
    ps.publish( "test", "hello world!" );
    `
],
      time: "2019-10-01",
      title: "几种常见的设计模式",
      items: [
        {
          title: "构造函数模式",
          target: "#constructor"
        },
        { divider: true },
        {
          title: "工厂模式",
          target: "#factory"
        },
        { divider: true },
        {
          title: "模块模式",
          target: "#module"
        },
        { divider: true },
        {
          title: "单例模式",
          target: "#single"
        },
        { divider: true },
        {
          title: "观察者模式",
          target: "#observer"
        },
        { divider: true },
        {
          title: "发布/订阅模式式",
          target: "#subpub"
        },
        { divider: true },
      ],
    };
  },
  mounted() {
  },
  methods: {}
};
</script>

<style lang="scss">
#designPattern {
  .blogcontent {
    padding-top: 20px;
  }
  .code {
    font-size: 12px;
    background-color: #f9f9f5;
    white-space: pre-wrap;
    word-wrap: break-word;
  }
}
</style>

